import { isNumber, getCurrentColor } from '../../utils';

export default {
	props: {
		icon: {
			type: String,
			default: 'cl-icon-favor-fill'
		},
		color: {
			type: [String, Array],
			default: '#409EFF'
		},
		voidColor: {
			type: String,
			default: '#C6D1DE'
		},
		size: {
			type: [Number, String],
			default: 40
		},
		value: {
			type: [Number, String],
			default: 0
		},
		max: {
			type: [Number, String],
			default: 5
		},
		disabled: {
			type: [Boolean, String],
			default: false
		},
		showText: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			value2: 0
		};
	},
	watch: {
		value: {
			immediate: true,
			handler(val) {
				this.value2 = val;
			}
		}
	},
	computed: {
		list() {
			return new Array(this.max).fill(1).map((e, i) => {
				let int = parseInt(this.value2);
				let dec = this.value2 - int;

				return {
					width: (this.value2 > i ? (int > i ? 100 : dec * 100) : 0) + '%',
					color: getCurrentColor({
						value: this.value2,
						color: this.color,
						max: this.max
					})
				};
			});
		}
	},
	methods: {
		onTap(index) {
			if (this.disabled) {
				return false;
			}

			this.value2 = index + 1;

			this.$emit('input', this.value2);
			this.$emit('change', this.value2);
		}
	}
};
